@mixin onMobile {
  @media (min-width: 320px) and (max-width: 767px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
